.starting {
  margin: 3rem auto;
  margin-top: 150px;
}

.starting h1 {
  font-size: 3rem;
  text-align: center;
}

.form {
    width: 30%;
    margin: auto;
    padding-top: 20px;
  }

  @media screen and (max-width: 1395px){
    .form {
      width: 40%;
    }
  }
  
  @media screen and (max-width: 985px){
    .form {
      width: 60%;
    }
  }
  
  @media screen and (max-width: 680px){
    .form {
      width: 70%;
    }
  }
  
  @media screen and (max-width: 500px){
    .form {
      width: 90%;
    }
  }
  
  .control {
    margin-bottom: 0.5rem;
  }
  
  .control label {
    display: block;
    font-weight: bold;
    margin-bottom: 0.5rem;
  }
  
  .control input,
  .control textarea {
    display: block;
    font: inherit;
    border-radius: 4px;
    border: 1px solid #ccc;
    padding: 0.25rem;
    width: 100%;
  }
  
  .actions {
    margin-top: 1.5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-weight: 600;
  }
  
  .actions button {
    cursor: pointer;
    font: inherit;
    color: white;
    background-color: #9f5ccc;
    border: 1px solid #9f5ccc;
    border-radius: 4px;
    padding: 0.5rem 2.5rem;
  }
  
  .actions button:hover {
    background-color: #873abb;
    border-color: #873abb;
  }