.card {
    background-color: white;
    border-radius: 6px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
    width: 30%;
    margin: auto;
  }

@media screen and (max-width: 1395px){
  .card {
    width: 40%;
  }
}

@media screen and (max-width: 985px){
  .card {
    width: 60%;
  }
}

@media screen and (max-width: 680px){
  .card {
    width: 70%;
  }
}

@media screen and (max-width: 500px){
  .card {
    width: 90%;
  }
}